import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '../views/Home.vue';
import BuyTickets from '../views/BuyTickets.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
  },
  {
    path: '/buy-tickets',
    name: 'BuyTickets',
    component: BuyTickets,
  },
  {
    path: '/success',
    name: 'Success',
    component: Home,
    props: {
      success: true,
    },
  },
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
});

export default router;
