<template>
  <div class="checkout">
    <div class="top">
      <Title :top="$t('checkout.add-email-title')" />
    </div>
    <div class="bottom">
      <template v-if="!token">
        <Result v-if="error">
          {{ $t('checkout.wrong-password') }}
        </Result>
        <Input
          type="password"
          :placeholder="$t('checkout.enter-password')"
          autocomplete="on"
          @submit="submitPassword"
        />
      </template>
      <template v-else>
        <Result v-if="error">
          {{ $t('checkout.add-email-error') }}
        </Result>
        <Result
          v-if="success"
          success
        >
          {{ $t('checkout.add-email-success') }}
        </Result>
        <Title :top="$t('checkout.add-email')" />
        <Input
          type="email"
          :placeholder="$t('checkout.placeholder')"
          @submit="submitEmail"
        />
      </template>
    </div>
  </div>
</template>

<script>
import { AES, enc } from 'crypto-js';
import Result from '@/components/Result.vue';
import Input from '@/components/Input.vue';
import Title from '@/components/Title.vue';

export default {
  components: {
    Result, Input, Title,
  },
  data: () => ({
    error: false,
    success: false,
    token: '',
  }),
  methods: {
    submitPassword(password) {
      this.token = AES.decrypt('U2FsdGVkX1/Y0rjVaaFgxWg5j08HlzRxcRJn0HjW8BIOMChihztpk9hyFNTVW3A6', password).toString(enc.Utf8);
      this.error = !this.token;
    },
    async submitEmail(email) {
      const response = await fetch(process.env.VUE_APP_EMAIL_ADD_URL, {
        method: 'post',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          data: {
            emails: [email],
            token: this.token,
          },
        }),
      });
      if (response.status !== 200) {
        console.warn('Response:', response);
        this.error = true;
      } else {
        this.success = true;
      }
    },
  },
};
</script>

