import Vue from 'vue';
import Vuex from 'vuex';
import { Magic } from 'magic-sdk';
import { ethers } from 'ethers';
import { RelayProvider, resolveConfigurationGSN } from '@opengsn/gsn';

Vue.use(Vuex);

const wyreRequest = async (path, options) => {
  const response = await fetch(`${process.env.VUE_APP_WYRE_API_URL}${path}`, {
    ...options,
    headers: {
      Authorization: `Bearer ${process.env.VUE_APP_WYRE_APY_SECRET}`,
      ...options.headers,
    },
  });
  if (response.status !== 200) throw new Error(`Invalid response status: ${response.status}`);
  return response.json();
};

const network = process.env.VUE_APP_IS_TEST_ENV
  ? { rpcUrl: 'https://kovan.infura.io/v3/f120ce838b754869bd115c524008df00', chainId: 42 }
  : { rpcUrl: 'https://mainnet.infura.io/v3/f120ce838b754869bd115c524008df00', chainId: 1 };

export default new Vuex.Store({
  state: {
    email: '',
    address: '',
    magicLinkProvider: null,
    metaMaskProvider: window.ethereum,
    provider: null,
    signer: null,
    isEnoughDai: false,
    isTicketBought: false,
  },
  getters: {
    dai: ({ signer }) => signer && new ethers.Contract(
      process.env.VUE_APP_DAI_ADDRESS,
      [
        'function balanceOf(address owner) view returns (uint256)',
        'function decimals() view returns (uint8)',
        'function allowance(address, address) view returns (uint256)',
        'function approve(address usr, uint wad) returns (bool)',
        'function nonces(address) view returns (uint)',
      ],
      signer,
    ),
  },
  mutations: {
    setAuthMagicLink(state, { email, provider } = {}) {
      state.email = email;
      state.magicLinkProvider = provider;
    },
    setAuthEthereum(state, { address, signer } = {}) {
      state.address = address;
      state.signer = signer;
    },
    setIsEnoughDai(state, isEnoughDai) {
      state.isEnoughDai = isEnoughDai;
    },
    setIsTicketBought(state, isTicketBought) {
      state.isTicketBought = isTicketBought;
    },
  },
  actions: {
    async authMagicLink({ commit, dispatch }, email) {
      const magic = new Magic(process.env.VUE_APP_MAGIC_LINK_API_KEY, { network });
      await magic.auth.loginWithMagicLink({ email });
      commit('setAuthMagicLink', { email, provider: magic.rpcProvider });
      if (!window.ethereum) await dispatch('authEthereum', 'magic-link');
    },
    async authEthereum({ state, commit, dispatch }, providerName) {
      let web3Provider;
      switch (providerName) {
        case 'magic-link': {
          web3Provider = state.magicLinkProvider;
          break;
        }
        case 'meta-mask':
          await state.metaMaskProvider.enable();
          web3Provider = state.metaMaskProvider;
          break;
        default:
          throw new Error('Unknown provider');
      }
      const gsnConfiguration = await resolveConfigurationGSN(web3Provider, {
        paymasterAddress: process.env.VUE_APP_TICKET_SALE_PAYMASTER_ADDRESS,
        methodSuffix: '_v4',
      });
      const provider = new ethers.providers.Web3Provider(
        new RelayProvider(web3Provider, gsnConfiguration),
      );
      const signer = provider.getSigner();
      commit('setAuthEthereum', { signer, address: await signer.getAddress() });
      await dispatch('fetchIsEnoughDai');
    },
    async checkTicketOwner({ state: { metaMaskProvider } }, email) {
      await metaMaskProvider.enable();
      const provider = new ethers.providers.Web3Provider(metaMaskProvider);
      const signer = provider.getSigner();
      const signature = await signer.signMessage(`I want to connect my NFT Ticket with the email address "${email}" to login in to the azimuth event.`);
      const response = await fetch(process.env.VUE_APP_TICKET_VALIDATION_URL, {
        method: 'post',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          data: {
            address: await signer.getAddress(),
            email,
            signature,
          },
        }),
      });
      if (response.status !== 200) console.warn('Response:', response);
      return response.status === 200;
    },
    async fetchIsEnoughDai({ state: { address }, getters: { dai }, commit }) {
      const ticketPrice = ethers.BigNumber.from(process.env.VUE_APP_TICKET_PRICE)
        .mul(ethers.BigNumber.from(10).pow(await dai.decimals()));
      const isEnoughDai = (await dai.balanceOf(address)).gte(ticketPrice);
      commit('setIsEnoughDai', isEnoughDai);
      return isEnoughDai;
    },
    async buyDai({ state: { email, address }, dispatch }) {
      const { reservation } = await wyreRequest('/v3/orders/reserve', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          referrerAccountId: process.env.VUE_APP_WYRE_ACCOUNT_ID,
          sourceCurrency: 'USD',
          destCurrency: 'DAI',
          destAmount: process.env.VUE_APP_TICKET_PRICE,
          amountIncludeFees: false,
          paymentMethod: 'debit-card',
          country: 'us',
          email,
          dest: `ethereum:${address}`,
          lockFields: ['email', 'dest'],
        }),
      });

      const widget = new window.Wyre({
        env: process.env.VUE_APP_IS_TEST_ENV ? 'test' : 'prod',
        reservation,
        operation: {
          type: 'debitcard-hosted-dialog',
        },
      });
      widget.open();
      await new Promise((resolve) => widget.on('paymentSuccess', resolve));

      /* eslint-disable no-await-in-loop */
      while (!(await dispatch('fetchIsEnoughDai'))) {
        await new Promise((resolve) => setTimeout(resolve, 5000));
      }
      /* eslint-enable no-await-in-loop */
    },
    async buyTicket({ state: { signer, address }, getters: { dai }, commit }) {
      const nonce = await dai.nonces(address);
      const expiry = Date.now() + 120;
      // eslint-disable-next-line no-underscore-dangle
      const signature = await signer._signTypedData({
        name: 'Dai Stablecoin',
        version: '1',
        chainId: network.chainId,
        verifyingContract: dai.address,
      }, {
        Permit: [
          { name: 'holder', type: 'address' },
          { name: 'spender', type: 'address' },
          { name: 'nonce', type: 'uint256' },
          { name: 'expiry', type: 'uint256' },
          { name: 'allowed', type: 'bool' },
        ],
      }, {
        holder: address,
        spender: process.env.VUE_APP_TICKET_SALE_ADDRESS,
        nonce,
        expiry,
        allowed: true,
      });
      const r = signature.slice(0, 66);
      const s = `0x${signature.slice(66, 130)}`;
      const v = `0x${signature.slice(130, 132)}`;

      const ticketSale = new ethers.Contract(
        process.env.VUE_APP_TICKET_SALE_ADDRESS,
        ['function permitAndPurchaseTicket(uint256 nonce, uint256 expiry, uint8 v, bytes32 r, bytes32 s)'],
        signer,
      );
      await (await ticketSale.permitAndPurchaseTicket(nonce, expiry, v, r, s)).wait();
      commit('setIsTicketBought', true);
    },
  },
});
