<template>
  <div class="input">
    <slot />
    <form @submit.prevent="submit(text)">
      <input
        v-model="text"
        v-bind="$attrs"
        required
      >
      <Button type="submit">
        {{ $t('checkout.continue') }}
      </Button>
    </form>
  </div>
</template>

<script>
import Button from '@/components/Button.vue';

export default {
  components: { Button },
  data: () => ({
    text: '',
  }),
  methods: {
    submit(text) {
      this.text = '';
      this.$emit('submit', text);
    },
  },
};
</script>

