<template>
  <div class="buy-tickets">
    <input
      v-model="email"
      type="email"
    >
    <button @click="buy('magic-link')">
      MagicLink
    </button>
    <button @click="buy('meta-mask')">
      MetaMask
    </button>
    {{ error ? 'Something went wrong' : '' }}

    <pre>
Test data:

Card number: 4111111111111111
Expiration: 10/23
CVV: 123
Address: 1600 Amphitheatre Parkway
City: Mountain View
State: California
ZIP code: 94043

Phone number: 14047772394 (https://www.receivesms.org/us-phone-number/3451/)
SMS code: 000000
Bank verification code: 000000
    </pre>
  </div>
</template>

<script>
export default {
  data: () => ({
    email: '',
    error: false,
  }),
  methods: {
    async buy(providerName) {
      this.error = false;
      try {
        await this.$store.dispatch('buyTickets', { providerName, email: this.email });
      } catch (error) {
        this.error = true;
        throw error;
      }
    },
  },
};
</script>

<style>
.buy-tickets > * {
  display: block;
}
</style>
